$color-ca: #01CAE4;
@mixin color-ca-fixed{
    background: $color-ca;
    color: white;
}
.swiper2{
    z-index: 200;
    height: auto;
    a.card-title{
        color: black;
        text-decoration: none;
        transition: .3s;
        &:hover{
            color: $color-ca;
        }
    }
    .e1e1e1{
        color: #f1f1f1;
    }
    .card_btn {
        a{
            color: $color-ca;
            border: 2px solid $color-ca;
            transition: .3s;
            border-radius: 8px;
            text-decoration: none;
           
            &:hover{
                @include color-ca-fixed;
            }
            &.active_a{
                @include color-ca-fixed
            }
        }
    }
    .swiper-button-prev2,
    .swiper-button-next2{
        width: 35px;
        background: white;
        padding: 5px;
        border-radius: 50%;
        box-shadow: 0 0 5px 2px #d3d3d3;
        transition: .3s;
        &:hover{
            @include color-ca-fixed;
            &::after{
                color: white!important;
            }
        }
        &::after{
            content: "←";
            font-size: 20px;
            color: rgba(85, 85, 85, 0.493);
            
        }

    }
    .img_wrapper {
        overflow: hidden;
       
        .card-img-top{
            transition: 1s;
        }
        .bookmark_link{
            right: -50%;
            top: 10px;
            transition: .5s;

        }
        &:hover{
            .card-img-top{
                transform: scale(1.1);
            }
            .bookmark_link{
                right: 5%;
                background: white;
                &:hover{
                    background: red;
                    
                }
            }
        }
    }
}