.schedule_page{
    .btn_slot{
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0,0,0,0.6);
        z-index: 2000;
    }
    .slot_hide{
        opacity: 0;
        height: 0;
        overflow: hidden;
        transition: .5s;
    }
    .slot_show{
        opacity: 1;
        height: 100vh;
        overflow: visible;

    }
}