
@import url('https://unpkg.com/swiper/css/swiper.min.css');

.swiper-container {
    height: 300px;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    .item{
        width: 150px;
        height: 150px;
        border-radius: 50%;
        box-shadow: 0px 0px  5px 1px rgba(0, 0, 0, 0.123);
        position: relative;
        #item_point{
          width: 15px;
          height: 15px;
          background: #0DE0FE;
          position: absolute;
          top: 65%;
          right: 0;
          border-radius: 50%;
          box-shadow: 0 0 0 3px white, 0 0 1px 5px #ddd;
        }
    }
  }

  .swiper-pagination-bullet{
      width: 25px!important;
      border-radius: 0px!important;
  }