header {
    i{
        font-size: 15px;
    }
    // Header Start
    &.header_fixed{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 10000;
        animation: 1s cubic-bezier(0.075, 0.82, 0.165, 1) header_fixed forwards;
        opacity: 0;
        @keyframes header_fixed{
            0%{
                transform: translateY(-50px);
                opacity: 0;
            } 
            60%{
                transform: translateY(0px);
                opacity: 1;
            }
            80%{
                transform: translateY(-25px);
                opacity: 1;

            }
            90%{
                transform: translateY(0px);
                opacity: 1;

            }
            95%{
                transform: translateY(10px);
                opacity: 1;

            }
            100%{
                transform: translateY(0px);
                opacity: 1;

            }

        }
    }
    .nav_palette{
        transition: .3s;
        &:hover{
            color: #2DE1B1!important;
        }
        .hover_palette{
            opacity: 0;
            pointer-events: none;
            user-select: none;
            transform: translateY(50px);
            transition: .5s;
            z-index: 100;
        }
        &:hover{
            .hover_palette{
                opacity: 1;
                pointer-events: visible;
                user-select: text;
                transform: translateY(0px);
                position: relative;
                &::before{
                    content: '';
                    position: absolute;
                    border: 15px solid ;
                    border-color: transparent transparent #f2f2f2 transparent;
                    top: -28px;
                    left: 20%;
                    z-index: 2;

                }
                a{
                    border: none;
                    border-bottom: 1px solid rgba(36, 36, 36, 0.116);
                    span{
                        transition: .3s;
                    display: inline-block;
                   
                    }
                    &:hover{
                              span{
                                color: #2DE1B1;
                                transform: translateX(15px)
                              }
                    }
                }
            }
        }
    }

   .third_palette_wrapper{
      
    .third_palette{
        top: 0;
        left: 100%;
        width: 200px;
        padding: 5px;
        background: white;
        opacity: 0;
        pointer-events: none;
        user-select: none;
        transition: .3s;
        transform: translateY(15px);
        a{
            display: block;
            color: black;
            &:hover{
                color: #2DE1B1;
            }
        }
        &::after{
            content: '';
            border: 10px solid;
            border-color: transparent #80808047 transparent transparent;
            top: 8%;
            left: -10%;
            position: absolute;
        }
    }
    &:hover{
        .third_palette{
            opacity: 1;
            pointer-events: visible;
            user-select: visible;
            transform: translateY(0px)
        }
    }
   }
    
}
.navbar-nav a{
    transition: .3s;
    &.active_color{
        color: #22ddee!important;
    }
    &:hover{
        color: #2DE1B1!important;
    }
}
.logSign a{
    border: 2px solid #2DE1B1;
    border-radius: 4px;
    transition: .3s;
    color: #2DE1B1;
    &:hover{
        background: #2DE1B1;
        color: white;
    }
    // header end

   
}

// for small-lg device......................
@media screen and (max-width: 767px){
    .toggle_list{
        z-index: 1000;
        position: fixed;
        top: 0;
        width: 100vw;
        height: 100vh;
        transition: 1s;
        pointer-events: none;
        opacity: 0;

    }
    .navbar{
        padding: 0!important;
    }
    .navbar-nav{
        width: 50%;
        background: #15558D;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
        transform: translateX(-100%);
        transition: 1s;
    }
    .hover_palette{
       all: initial!important;
       &::before{
           display: none!important;
       }
       a{
           all: initial;
           transform:translateX(15px);
           padding: 10px;
       }
    }

    
.sm_toggle_mobile{
    pointer-events: visible;
    background: rgba(0, 0, 0, 0.336);
    opacity: 1;
    .navbar-nav{
        transform: translateX(0%)

    }
    li{
        a{
            color: white!important;
            padding-left: 12px!important;
            padding-right: 12px!important;
            border-bottom: 1px solid rgba(255, 255, 255, 0.329);
            display: flex;
            justify-content: space-between;
            background: transparent!important;

        }
    }
    
}
.logImg{
    background: white !important;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
    button{
        background: #00000040;
        color: white;
        padding: 10px 15px;
        border: none;
        outline: none;
        z-index: 100;
        margin: 5px;
        border-radius: 5px;
    }
    button:focus{
        outline: none;
    }
}

}