//################### Global Settings ###############
@import url('https://fonts.googleapis.com/css?family=Poppins&display=swap');
*{
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    line-height: 28px;
}
html,body{
    margin: 0;
    padding: 0;
}
ul,ol,li{
    list-style: none;
}
a{
    color: black;
    text-decoration: none!important;
}

//##################### Global Variable ##################



$color_ca: #01CAE4;

//################## Mixin Style ##################


//################ Custom Color ###############
.star_color{
    color: #F4C150;
}
.color_ca{
    color: #01CAE4
}
.bg_ca{
    background: #01CAE4!important;
}
.footer_bg_color{
    background: #15558D!important;
}
// ################## Custom Border #############
.border_ca{
    border: 1px solid #01CAE4;
}
// ################# Custom Font ###############
.font_size_-8rem{
    font-size: .8rem;
}

//############# Shadow Style ###########
.shadow_white_small{
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.322);
}

//############## Width Custom #########
.w_200{
    width: 200px;
}
.w_20{
    width: 20%!important;
}

//############# Position Custom ###########

.top_50{
    top: 50%;
}
.top_transform_center{
    transform: translateY(-50%);
}

// ################# Custom Flexbox ############
.flex_basis_100{
    flex-basis: 100%!important;
}

// ............... Position center..........

.position_center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}