.patient_count{
    font-size: 25px;
}
.dashboard_table_wrapper{
    font-size: .9rem;
    overflow-x: auto;
    table{
        width: 70rem;
        thead{
            tr{
                th{
                    text-align: left;
                    padding: 0.5rem;
                }
            }
        }
        tbody{
            tr{
                transition: .1s;
                td{
                    padding: 1.2rem;
                    img{
                        width: 40px;
                        height: 40px;
                    }
                }
             &:hover{
                    background: rgba(0, 0, 0, 0.096);
                }
            }
        }
    }
}