
$color_ca: #01CAE4;
.section_5{
    
    .img_wrapper {
        overflow: hidden;
       
        .card-img-top{
            transition: 1s;
        }
        
        &:hover{
            .card-img-top{
                transform: scale(1.1);
            }
           
        }
    }
    a{
        color: black;
        transition: .5s;
        text-decoration: none;
        &:hover{
            color: $color_ca;
        }
    }
}