
$appointBorder: 1px solid rgba(73, 73, 73, 0.1);


    .top_control{
        color: #fff!important;
        a{
            color: #fff!important;
            text-decoration: none!important;

        }
    }
.appointment_profile{
    border: 1px solid rgba(121, 120, 120, 0.1);
    .profile_top{
        text-align: center;
        a.profile_anchor{
            width: 140px;
            height: 140px;
            border-radius: 100%;
            display: inline-block;
            img{
                width: 100%;
                height: 100%;
                border-radius: 100%;
            }
        }
    }
    .profile_more_view{
        ul {
            li {
                a{
                    border: 1px solid rgba(73, 73, 73, 0.1);
                    text-decoration: none;
                    font-size: 14px;
                    color: #444;
                    transition: .3s;
                    &:hover{
                        color: #58E2FE;
                    }
                }
            }
        }
    }
}
.appointment_card{
    border: $appointBorder;
    .img_itme{
        img{
            width: 120px;
            height: 120px;
        }
    }
   
    .person_name{
        color: #444;
        text-decoration: none;
        &:hover{
            color: #09E2D4;
        }
    }
}

.btn_list_wrapper {
    button{
        display: inline-block;
        padding: 3px;
        border: 0;
        outline: 0;
        font-size: 13px;
        &:nth-of-type(1){
            background: #E0F6F6;
            color: #59CDE3;
        }
        &:nth-of-type(2){
            background: #E1F6ED;
            color: #5AAF48;
        }
        &:nth-of-type(3){
            background: #FEE2E6;
            color: #E63C3C;
        }
        &:hover{
            opacity: 0.8
        }
    }
}

